import React, { Component } from 'react';
import {Button} from 'react-bootstrap';
import {FormattedMessage} from 'react-intl';

class TermsAndConditions extends Component {
    constructor(props) {
        super(props);

        this.state = {
            "termsAndConditions": typeof(props.termsAndConditions) !== "undefined" ? props.termsAndConditions : ""
        };

        this.onAccept = typeof(props.onAccept) !== "undefined" ? props.onAccept : () => {};
        this.onDecline = typeof(props.onDecline) !== "undefined" ? props.onDecline : () => {};

        this.domKey = ""+Math.random();
    }

    insertTermsAndConditions() {
        var element = document.getElementById("terms-and-conditions-text" + this.domKey);
        if (typeof(element) !== "undefined") {
            element.innerHTML = this.state.termsAndConditions;
        }
    }

    componentDidMount() {
        this.insertTermsAndConditions();
    }

    componentDidUpdate() {
        this.insertTermsAndConditions();
    }

    render() {
        return (
            <div>
                <div className="terms-and-conditions-text-and-title">
                    <h1>
                        <FormattedMessage
                            id="termsAndConditionsPopupTitle"
                            defaultMessage="Terms and Conditions"
                        />
                    </h1>
                    <div id={"terms-and-conditions-text" + this.domKey} className="terms-and-conditions-text-area"></div>
                </div>
                <div className="terms-and-conditions-buttons">
                    <Button onClick={() => {this.onAccept();}} className="btn btn-success terms-and-conditions-buttons">
                        <FormattedMessage
                            id="iAgreeButton"
                            defaultMessage="I Agree"
                        />
                    </Button>
                    <Button onClick={() => {this.onDecline();}} className="btn btn-danger terms-and-conditions-buttons">
                        <FormattedMessage
                            id="iDoNotAgreeButton"
                            defaultMessage="I Do Not Agree"
                        />
                    </Button>
                </div>
            </div>
        )
    }
}

export default TermsAndConditions;
