import React from 'react';
import ReactDOM from 'react-dom';
import ReactiveLoginApp from './ReactiveLoginApp';
import ForgotPasswordPage from './ForgotPasswordPage';
import './index.css';
import ClaimAccountsPage from "./ClaimAccountsPage";
import InvalidEmailCodePage from "./InvalidEmailCodePage";

(function () {
    const applyForgotPassword = function (
        root, props
    ) {
        ReactDOM.render(
            <ForgotPasswordPage {...props}/>,
            root
        );
    };

    const applyClaimAccounts = function (
        root, props
    ) {
        ReactDOM.render(
            <ClaimAccountsPage {...props}/>,
            root
        );
    };

    const applyInvalidEmailCode = function(
        root, props
    ) {
        ReactDOM.render(
            <InvalidEmailCodePage {...props}/>,
            root
        );
    };

    const applyReactiveLogin = function (
        root,
        authBaseUri,
        lang,
        theme,
        termsAndConditions,
        clientId,
        authorizeUri,
        codeAuthorizeUri,
        username,
        email,
        emailJwt,
        logoutUri,
        abortUri,
        idpUrisJson,
        authorizeType,
        extParams,
        authorizationCodeCallback,
        enterpriseLogins
    ) {
        ReactDOM.render(
            <ReactiveLoginApp
                lang={lang}
                theme={theme}
                authBaseUri={authBaseUri}
                termsAndConditions={termsAndConditions}
                clientId={clientId}
                authorizeUri={authorizeUri}
                codeAuthorizeUri={codeAuthorizeUri}
                authorizeType={authorizeType}
                username={username}
                email={email}
                emailJwt={emailJwt}
                logoutUri={logoutUri}
                abortUri={abortUri}
                idpUrisJson={idpUrisJson}
                extParams={extParams}
                authorizationCodeCallback={authorizationCodeCallback}
                enterpriseLogins={enterpriseLogins}
            />,
            root
        );
    };

    const applyReactiveLoginFromMeta = function (root) {
        const authBaseUri = "";
        const lang = ReactiveLoginApp.getMeta("languageAlpha2");
        const theme = ReactiveLoginApp.getMeta("theme");
        const termsAndConditions = ReactiveLoginApp.getMeta("termsAndConditions");
        const clientId = ReactiveLoginApp.getMeta("clientId");
        const authorizeUri = ReactiveLoginApp.getMeta("authorizeUri");
        const codeAuthorizeUri = ReactiveLoginApp.getMeta("codeAuthorizeUri");
        const authorizeType = ReactiveLoginApp.getMeta("authorizeType");
        const username = ReactiveLoginApp.getMeta("username");
        const email = ReactiveLoginApp.getMeta("email");
        const emailJwt = ReactiveLoginApp.getMeta("emailJwt");
        const logoutUri = ReactiveLoginApp.getMeta("logoutUri");
        const abortUri = ReactiveLoginApp.getMeta("abortUri");
        const idpUrisJson = ReactiveLoginApp.getMeta("idpUrisJson");

        applyReactiveLogin(
            root,

            authBaseUri,
            lang,
            theme,
            termsAndConditions,
            clientId,
            authorizeUri,
            codeAuthorizeUri,
            username,
            email,
            emailJwt,
            logoutUri,
            abortUri,
            idpUrisJson,
            authorizeType,
            /*extParams=*/ {},
            /*authorizationCodeCallback=*/ undefined,
            /*enterpriseLogins=*/ undefined
        );
    };

    (function () {
        var rootElement = document.getElementById('reactiveLoginRootElement');
        if (typeof(rootElement) !== 'undefined' && rootElement) {
            applyReactiveLoginFromMeta(rootElement);
        }
    })();

    window.applyForgotPassword = applyForgotPassword;
    window.applyClaimAccounts = applyClaimAccounts;
    window.applyInvalidEmailCode = applyInvalidEmailCode;
    window.applyReactiveLogin = applyReactiveLogin;
})();