
import querystring from 'querystring';
import axios from 'axios';

const messages = {
    failedToAcceptTermsAndConditions: {
        'id': 'failedToAcceptTermsAndConditions',
        'defaultMessage': "Failed to accept terms and conditions"
    },
    errorCodeAcceptTermsAndConditions: {
        'id': 'errorCodeAcceptTermsAndConditions',
        'defaultMessage': "Error response code {code} on accept terms and conditions. Please try again later."
    },
    failedToRunAuthorize: {
        'id': 'failedToRunAuthorize',
        'defaultMessage': "Failed to run authorize request"
    },
    errorCodeAuthorize: {
        'id': 'errorCodeAuthorize',
        'defaultMessage': "Error response code {code} on authorize endpoint. Please try again later."
    },
    unexpectedAuthorizeResponse: {
        'id': 'unexpectedAuthorizeResponse',
        'defaultMessage': 'Unexpected response from authorize endpoint'
    },
    autoLinkAccountFailure: {
        'id': 'autoLinkAccountFailure',
        'defaultMessage': 'Auto link account failure'
    },
    strangeResponseAutoLinkAccount: {
        'id': 'strangeResponseAutoLinkAccount',
        'defaultMessage': 'Strange response from auto link account endpoint'
    },
    autoLinkDidNotIncludeIdentity: {
        'id': 'autoLinkDidNotIncludeIdentity',
        'defaultMessage': 'Auto link account did not include an identity id in the response'
    },
    finalStepInLoginFailed: {
        'id': 'finalStepInLoginFailed',
        'defaultMessage': 'Final step in login process failed'
    },
    finalStepInLoginFailedProduceJwt: {
        'id': 'finalStepInLoginFailedProduceJwt',
        'defaultMessage': 'Final login step did not produce a JWT'
    },
    termsAndConditionsStatusFailed: {
        'id': 'termsAndConditionsStatusFailed',
        'defaultMessage': "Terms and conditions status request failed"
    }
};

class LoginOperations {
    constructor(props) {
        this.intl = props.intl;
        this.authorizationCodeCallback = typeof(props.authorizationCodeCallback) === 'function' ? props.authorizationCodeCallback : undefined;
        this.authBaseUri = props.authBaseUri;
        this.clientId = props.clientId;
        this.codeAuthorizeUri = props.codeAuthorizeUri;
        this.onEmailConflict = typeof(props.onEmailConflict) === 'function' ? props.onEmailConflict : function (payload, intermediateToken) {};
        this.onTermsAndConditions = typeof(props.onTermsAndConditions) === 'function' ? props.onTermsAndConditions : function (jwt, termsAndConditions) {};
    }

    loggedIn(language, jwt) {
        const {formatMessage} = this.intl;

        var clientId = this.clientId;
        axios.get(this.authBaseUri+"/v1/login/termsandconditions/status?"+querystring.stringify({"clientId": clientId, "language": language}), {
                'headers' : {
                    'Authorization': 'Bearer '+jwt
                }
            }
        )
            .then((response) => {
                var tcStatus = response.data;
                var accepted = typeof(tcStatus) !== "undefined" && typeof(tcStatus.accepted) !== "undefined" && tcStatus.accepted;
                var termsAndConditions = typeof(tcStatus) !== "undefined" && typeof(tcStatus.termsAndConditions) !== "undefined" && tcStatus.termsAndConditions ? tcStatus.termsAndConditions : "";
                if (accepted) {
                    this.authorize(jwt);
                } else {
                    this.onTermsAndConditions(jwt, termsAndConditions);
                    //this.setState({
                    //    "mode": "termsandconditions",
                    //    "jwt": jwt,
                    //    "termsAndConditions": termsAndConditions
                    //});
                }
            })
            .catch((error) => {
                alert(formatMessage(messages.termsAndConditionsStatusFailed));
            });
    }

    loginWithIdentity(language, intermediateToken, identityId) {
        const {formatMessage} = this.intl;

        axios.post(
            this.authBaseUri+'/v1/login/complete', querystring.stringify({}), {
                'headers' : {
                    'Authorization': 'Bearer '+intermediateToken
                },
                'xsrfCookieName': null,
                'withCredentials': true
            }
        )
            .then((response) => {
                var loginResponse = response.data;
                var jwt = loginResponse.jwt;
                if (typeof(jwt) !== 'undefined' && jwt) {
                    this.loggedIn(language, jwt);
                } else {
                    alert(formatMessage(messages.finalStepInLoginFailedProduceJwt));
                }
            })
            .catch((error) => {
                alert(formatMessage(messages.finalStepInLoginFailed));
            });
    }
    autoConnectIdentity(language, intermediateToken) {
        const {formatMessage} = this.intl;

        axios.post(
            this.authBaseUri+'/v1/login/idconnect/auto', querystring.stringify({}), {
                'headers' : {
                    'Authorization': 'Bearer '+intermediateToken
                }
            }
        )
            .then((response) => {
                var accountData = response.data;
                var identities = accountData.identities;
                if (typeof(identities) !== 'undefined' && identities.length === 1) {
                    var identity = identities[0];
                    var identityId = identity.id;
                    if (typeof(identityId) !== 'undefined' && identityId) {
                        this.loginWithIdentity(language, intermediateToken, identityId);
                    } else {
                        alert(formatMessage(messages.autoLinkDidNotIncludeIdentity));
                    }
                } else {
                    alert(formatMessage(messages.strangeResponseAutoLinkAccount));
                }
            })
            .catch((error) => {
                if (error.response && error.response.status === 409) {
                    /* 409=conflict */
                    const payload = error.response.data;
                    if (payload && typeof(payload.length) !== "undefined" && payload.length > 0) {
                        const item = payload[0];
                        if (
                            item &&
                            typeof(item.credentialsId) !== "undefined" &&
                            typeof(item.email) !== "undefined" &&
                            item.type === "email-conflict" &&
                            typeof(item.conflictingIdentity) !== "undefined" &&
                            typeof(item.conflictingIdentity.id) !== "undefined"
                        ) {
                            this.onEmailConflict(payload, intermediateToken);
                            //this.setState({"idpEmailConflict": payload, "idpEmailToken": intermediateToken});
                            return;
                        }
                    }
                }
                alert(formatMessage(messages.autoLinkAccountFailure));
            });
    }

    authorize(intermediateToken) {
        const {formatMessage} = this.intl;

        var codeAuthorizeUri = this.codeAuthorizeUri;
        axios.get(
            this.authBaseUri+codeAuthorizeUri, {
                'headers': {
                    'Authorization': 'Bearer ' + intermediateToken
                }
            }
        )
            .then((response) => {
                var authorizeData = response.data;
                if (typeof(authorizeData) !== 'undefined' && typeof(authorizeData.type) !== 'undefined' && authorizeData.type === 'redirect' && typeof(authorizeData.redirect) !== 'undefined') {
                    this.authorizationRedirect(authorizeData.redirect);
                } else {
                    alert(formatMessage(messages.unexpectedAuthorizeResponse));
                }
            })
            .catch((error) => {
                var response = error.response;
                if (typeof(response) !== 'undefined') {
                    alert(formatMessage(messages.errorCodeAuthorize, {'code': response.status}));
                } else {
                    alert(formatMessage(messages.failedToRunAuthorize));
                }
            })
    }

    authorizationRedirect(redirect) {
        if (typeof(this.authorizationCodeCallback) === 'function') {
            const queryPos = redirect.indexOf('?');
            let params = {};
            if (queryPos >= 0) {
                const query = redirect.substring(queryPos + 1);
                const pairs = query.split('&')
                    .map(function (pair) {
                        const eqPos = pair.indexOf('=');
                        if (eqPos >= 0) {
                            return [pair.substring(0, eqPos), pair.substring(eqPos + 1)];
                        } else {
                            return [pair, ''];
                        }
                    })
                    .map(function (pair) {
                        return pair.map(decodeURIComponent);
                    });
                pairs.forEach((pair) => {
                    params[pair[0]] = pair[1];
                });
            }
            if (typeof(params.code) !== 'undefined' && params.code) {
                this.authorizationCodeCallback(params.code);
                return;
            }

        }
        window.location.href = redirect;
    }

    acceptTermsAndConditions(jwt) {
        const {formatMessage} = this.intl;

        var clientId = this.clientId;
        axios.post(this.authBaseUri+"/v1/login/termsandconditions/accept", querystring.stringify({
            "clientId": clientId,
            "accepted": true
        }), {
            'headers' : {
                'Authorization': 'Bearer '+jwt
            }
        })
            .then((response) => {
                this.authorize(jwt);
            })
            .catch((error) => {
                var response = error.response;
                if (typeof(response) !== 'undefined') {
                    alert(formatMessage(messages.errorCodeAcceptTermsAndConditions, {'code': response.status}));
                } else {
                    alert(formatMessage(messages.failedToAcceptTermsAndConditions));
                }
            });
    }
}

export default LoginOperations;
