import React, { Component } from 'react';
import {injectIntl, intlShape} from "react-intl";

class EnterpriseLogins extends Component {
    constructor(props) {
        super(props);
    }

    close() {
        if (typeof(this.props.onClose) === 'function') {
            this.props.onClose();
        }
    }

    openLogin(login) {
        const width = 800;
        const height = 600;
        return window.open(
            login.uri,

            "targetWindow",

            "toolbar=no, " +
            "location=no, " +
            "status=no, " +
            "menubar=no, " +
            "scrollbars=yes, " +
            "resizable=yes, " +
            "width="+width+", " +
            "height="+height
        );
    }

    renderLogins() {
        return this.props.enterpriseLogins
            .map((login) => {
                return (
                    <li>
                        <a href={"#"} onClick={(event) => { event.preventDefault(); this.openLogin(login) }}>
                            <h2>{login.name}</h2>
                            <p>Log in to {login.name}</p>
                        </a>
                    </li>
                )
            })
    }

    render() {
        return (
            <div>
                <h1>Login to company account</h1>
                <p><a href={""} onClick={() => { this.close() }}>Close</a></p>
                <ul>
                    {(() => { return this.renderLogins() })()}
                </ul>
            </div>
        );
    }
}

EnterpriseLogins.propTypes = {
    intl: intlShape.isRequired,
};

const EnterpriseLoginsClass = EnterpriseLogins;

export {EnterpriseLoginsClass};
export default injectIntl(EnterpriseLogins);
