
import React, {Component} from 'react';
import {FormattedMessage, intlShape, injectIntl, defineMessages} from 'react-intl';

class InvalidEmailCode extends Component {
    render() {
        return (
            <div className="reactive-auth-login">
                <h1>
                    <FormattedMessage id={'invalidOrExpiredEmailCodeDescription'} defaultMessage={'The link has expired'} />
                </h1>
                <p>
                    <FormattedMessage id={'invalidOrExpiredEmailCodeDescription'} defaultMessage={'We are very sorry, but this link is either invalid or it has expired. If you did forget your password please try the "forgot password" link on the login page again to get a new link. Otherwise try logging in again in order to resolve this issue.'} />
                </p>
            </div>
        )
    }
}

InvalidEmailCode.propTypes = {
    intl: intlShape.isRequired
};

export default injectIntl(InvalidEmailCode);