import React, { Component } from 'react';

import {FormattedMessage, intlShape, injectIntl, defineMessages} from 'react-intl';

const messages = defineMessages({
    loginButton: {
        'id': 'loginButton',
        'defaultMessage': 'Log in'
    },
    usernamePlaceholder: {
        'id': 'usernamePlaceholder',
        'defaultMessage': '(Username or email)'
    },
    passwordPlaceholder: {
        'id': 'passwordPlaceholder',
        'defaultMessage': '(Password)'
    }
});

class UserPasswordLogin extends Component {
    constructor(props) {
        super(props);

        this.state = {
            "loginError": typeof(props.loginError) !== "undefined" ? props.loginError : false
        };

        this.onLogin = typeof(props.onLogin) !== 'undefined' ? props.onLogin : (username, password) => {};
        this.onForgotPassword = typeof(props.onForgotPassword) !== 'undefined' ? props.onForgotPassword : () => {};
        this.onSignup = typeof(props.onSignup) !== 'undefined' ? props.onSignup : null;

        this.username = typeof(props.username) !== "undefined" ? props.username : "";
        this.password = typeof(props.password) !== "undefined" ? props.password : "";

        this.usernameFieldDisabled = typeof(this.props.usernameFieldDisabled) !== 'undefined' ? this.props.usernameFieldDisabled : false;

        this.domKey = ""+Math.random();
    }

    clickLogIn(event) {
        var username = document.getElementById('username' + this.domKey).value;
        var password = document.getElementById('password' + this.domKey).value;
        event.preventDefault();
        this.onLogin(username, password);
        return false;
    }

    clickForgotPassword(event) {
        event.preventDefault();
        this.onForgotPassword();
        return false;
    }

    clickSignup(event) {
        event.preventDefault();
        this.onSignup();
        return false;
    }

    componentDidMount() {
        document.getElementById('username' + this.domKey).value = this.username;
        document.getElementById('password' + this.domKey).value = this.password;
    }

    render() {
        const {formatMessage} = this.props.intl;

        return (
            <form onSubmit={(event) => { return this.clickLogIn(event); }} method="POST">
                {
                    (() => {
                        if (this.state.loginError) {
                            return (
                                <div className="error col-sm-12 alert alert-danger">
                                    <FormattedMessage id="loginFailedError" defaultMessage="Login failed! Please check your username and password." />
                                </div>
                            );
                        } else {
                            return (
                                <span></span>
                            );
                        }
                    })()
                }
                <input key="username-can-change" className="col-sm-12" placeholder={formatMessage(messages.usernamePlaceholder)} type="text" id={"username" + this.domKey} name="username" disabled={ this.usernameFieldDisabled ? 'disabled' : undefined} />
                <input key="password-can-change" className="col-sm-12" placeholder={formatMessage(messages.passwordPlaceholder)} type="password" id={"password" + this.domKey} name="password" />
                <input className="col-sm-12" onClick={(event) => { return this.clickLogIn(event); }} type="submit" value={formatMessage(messages.loginButton)} />

                <p className="formlinks">
                    <a onClick={(event) => { return this.clickForgotPassword(event); }} href="#" className="float-left">
                        <FormattedMessage
                            id="forgotPasswordLink"
                            defaultMessage="Forgot password?"
                        />
                    </a>
                    {(() => {
                        if (this.onSignup !== null) {
                            return (
                                <a onClick={(event) => { return this.clickSignup(event); }} href="#" className="float-right">
                                    <FormattedMessage
                                        id="createNewAccountLink"
                                        defaultMessage="Create new account"
                                    />
                                </a>
                            );
                        } else {
                            return null;
                        }
                    })()}
                </p>
            </form>
        );
    }
}

UserPasswordLogin.propTypes = {
    intl: intlShape.isRequired,
};

export default injectIntl(UserPasswordLogin);
