
import React, {Component} from 'react';
import {FormattedMessage, intlShape, injectIntl, defineMessages} from 'react-intl';
import axios from "axios";

const messages = defineMessages({
    signupPasswordFieldPlaceholder: {
        'id': 'signupPasswordFieldPlaceholder',
        'defaultMessage': '(Choose a password)'
    },
    signupRepeatPasswordFieldPlaceholder: {
        'id': 'signupRepeatPasswordFieldPlaceholder',
        'defaultMessage': '(Repeat password)'
    },
    passwordsDoNotMatchError:{
        'id': 'passwordsDoNotMatchError',
        'defaultMessage': 'Passwords do not match.'
    },
    passwordTooShortError:{
        'id': 'passwordTooShortError',
        'defaultMessage': 'The password is too short. Minimum 6 characters.'
    }
});

class NewPassword extends Component {
    constructor(props) {
        super(props);
        this.state = {
            'errors': {}
        };
    }

    setPasswordFieldType(type) {
        var fields = document.getElementsByClassName("password-field");
        for (var i = 0; fields.length; i++) {
            fields.item(i).type = type;
        }
    }
    showPasswords() {
        this.setPasswordFieldType('text');
    }
    hidePasswords() {
        this.setPasswordFieldType('password');
    }

    getPassword() {
        const password = this.passwordField.value;
        return password;
    }

    getRepeatedPassword() {
        const repeatedPassword = this.repeatPasswordField.value;
        return repeatedPassword;
    }

    validatePassword(consumer) {
        const {formatMessage} = this.props.intl;
        const password = this.getPassword();
        const repeatedPassword = this.getRepeatedPassword();

        if (password === repeatedPassword) {
            if (password.length >= 6) {
                consumer(password);
            } else {
                this.setState({"errors": {"password": formatMessage(messages.passwordTooShortError)}});
            }
        } else {
            this.setState({"errors": {"password": formatMessage(messages.passwordsDoNotMatchError)}});
        }
    }

    render() {
        const {formatMessage} = this.props.intl;

        return (
            <div>
                {
                    (() => {
                        if (typeof (this.state.errors) !== "undefined" && typeof (this.state.errors.password) !== "undefined") {
                            return (
                                <div
                                    className="error col-sm-12 alert alert-danger">{this.state.errors.password}</div>
                            );
                        } else {
                            return (<span></span>);
                        }
                    })()
                }

                <input className="col-sm-12 password-field" type="password" ref={(ref) => { this.passwordField = ref; }}
                       placeholder={formatMessage(messages.signupPasswordFieldPlaceholder)} name="password"/>
                <input className="col-sm-12 password-field" type="password" ref={(ref) => { this.repeatPasswordField = ref; }}
                       placeholder={formatMessage(messages.signupRepeatPasswordFieldPlaceholder)}
                       name="repeatpassword"/>
                <div className="col-sm-2 eyeholder" onMouseDown={() => {
                    this.showPasswords();
                }} onMouseUp={() => {
                    this.hidePasswords();
                }} onMouseOut={() => {
                    this.hidePasswords();
                }}></div>
            </div>
        );
    }
}

NewPassword.propTypes = {
    intl: intlShape.isRequired
};

export default injectIntl(NewPassword, {withRef: true});