
import React, {Component} from 'react';
import getMessages from './lang';
import {IntlProvider} from "react-intl";
import ClaimAccounts from './ClaimAccounts';

class ClaimAccountsPage extends Component {
    render() {

        const lang = typeof(this.props.lang) !== 'undefined' ? this.props.lang : 'en';
        const messages = getMessages(lang);

        return (
            <IntlProvider locale={lang} messages={messages}>
                <ClaimAccounts {...this.props}/>
            </IntlProvider>
        );
    }
}

export default ClaimAccountsPage;