/* eslint-disable import/first */

import areIntlLocalesSupported from 'intl-locales-supported';

var localesMyAppSupports = [
    "en", "nb", "nn", "sv"
];

if (global.Intl) {
    // Determine if the built-in `Intl` has the locale data we need.
    if (!areIntlLocalesSupported(localesMyAppSupports)) {
        // `Intl` exists, but it doesn't have the data we need, so load the
        // polyfill and replace the constructors with need with the polyfill's.
        var IntlPolyfill = require('intl');
        Intl.NumberFormat   = IntlPolyfill.NumberFormat;
        Intl.DateTimeFormat = IntlPolyfill.DateTimeFormat;
    }
} else {
    // No `Intl`, so use and load the polyfill.
    global.Intl = require('intl');
}

import React, { Component } from 'react';
import './App.css';

import ReactiveLogin from './ReactiveLogin';

import {IntlProvider} from 'react-intl';
import getMessages from './lang';

class ReactiveLoginApp extends Component {
    constructor(props) {
        super(props);
    }

    static getElementsByName(name) {
        var nodeList = document.getElementsByName(name);
        var elements = [];
        for (var i = 0; i < nodeList.length; i++) {
            elements[i] = nodeList.item(i);
        }
        return elements;
    }
    static getMeta(name) {
        var matches =  ReactiveLoginApp.getElementsByName(name)
            .filter(element => typeof(element.tagName) !== 'undefined')
            .filter(element => element.tagName.toLowerCase() === "meta")
            .map(element => element.getAttribute("content"))
            .filter(value => typeof(value) !== "undefined")
            .filter(value => value);
        if (matches.length > 0) {
            return matches[0];
        } else {
            return null;
        }
    }
    render() {
        const lang = this.props.lang;
        const theme = this.props.theme;

        const m = getMessages(lang, theme);

        const authBaseUri = this.props.authBaseUri;
        const termsAndConditions = this.props.termsAndConditions;
        const clientId = this.props.clientId;
        const authorizeUri = this.props.authorizeUri;
        const codeAuthorizeUri = this.props.codeAuthorizeUri;
        const authorizeType = this.props.authorizeType;
        const username = this.props.username;
        const email = this.props.email;
        const emailJwt = this.props.emailJwt;
        const logoutUri = this.props.logoutUri;
        const abortUri = this.props.abortUri;
        const idpUrisJson = this.props.idpUrisJson;
        const extParams = this.props.extParams;
        const authorizationCodeCallback = typeof(this.props.authorizationCodeCallback) === 'function' ? this.props.authorizationCodeCallback : undefined;
        const enterpriseLogins = this.props.enterpriseLogins;
        return (
            <IntlProvider locale={lang} messages={m}>
                <ReactiveLogin
                    authBaseUri={authBaseUri}
                    language={lang}
                    termsAndConditions={termsAndConditions}
                    clientId={clientId}
                    authorizeUri={authorizeUri}
                    codeAuthorizeUri={codeAuthorizeUri}
                    authorizeType={authorizeType}
                    username={username}
                    email={email}
                    emailJwt={emailJwt}
                    logoutUri={logoutUri}
                    abortUri={abortUri}
                    idpUrisJson={idpUrisJson}
                    extParams={extParams}
                    authorizationCodeCallback={authorizationCodeCallback}
                    enterpriseLogins={enterpriseLogins}
                />
            </IntlProvider>
        );
    }
}

export default ReactiveLoginApp;
