import React, { Component } from 'react';
import {Modal, ModalBody, ModalFooter, ModalHeader, ModalTitle, Button} from 'react-bootstrap';
import {FormattedMessage, intlShape, injectIntl, defineMessages} from 'react-intl';
import Loader from 'react-loader-spinner';

const messages = defineMessages({
    acceptTermsAndConditionsError: {
        'id': 'acceptTermsAndConditionsError',
        'defaultMessage': 'You need to accept the Terms and Conditions.'
    },
    emailRequiredError:{
        'id': 'emailRequiredError',
        'defaultMessage': 'Email address is required.'
    },
    passwordRequiredError:{
        'id': 'passwordRequiredError',
        'defaultMessage': 'Password is required.'
    },
    passwordsDoNotMatchError:{
        'id': 'passwordsDoNotMatchError',
        'defaultMessage': 'Passwords do not match.'
    },
    signupEmailFieldPlaceholder: {
        'id': 'signupEmailFieldPlaceholder',
        'defaultMessage': '(E-Mail)'
    },
    signupPasswordFieldPlaceholder: {
        'id': 'signupPasswordFieldPlaceholder',
        'defaultMessage': '(Choose a password)'
    },
    signupRepeatPasswordFieldPlaceholder: {
        'id': 'signupRepeatPasswordFieldPlaceholder',
        'defaultMessage': '(Repeat password)'
    },
    signupButton: {
        'id': 'signupButton',
        'defaultMessage': 'Sign up'
    }
});

class SignupForm extends Component {
    constructor(props) {
        super(props);

        this.state = {
            "showTermsAndConditions": false,
            "termsAndConditions": typeof(props.termsAndConditions) !== "undefined" ? props.termsAndConditions : false,
            "errors": typeof(props.errors) !== "undefined" ? props.errors : {},
            "spinner": this.props.autoSubmit && (typeof(props.errors) === "undefined" || !props.errors)
        };

        this.onLogin = typeof(props.onLogin) !== "undefined" ? props.onLogin : () => {};
        this.onSignup = typeof(props.onSignup) !== "undefined" ? props.onSignup : (email, password, termsAndConditionsAccepted) => {};
        this.onTermsAndConditionsRequired = typeof(props.onTermsAndConditionsRequired) !== 'undefined' ? props.onTermsAndConditionsRequired : (termsAndConditions, callback) => { callback(false); };

        this.domKey = ""+Math.random();

        this.prefillDone = false;
        this.autoSubmitDone = false;
        this.fields = typeof(props.fields) !== "undefined" ? props.fields : {};
    }

    clickSignup(event) {
        const {formatMessage} = this.props.intl;

        if (event !== null) {
            event.preventDefault();
        }
        this.setState({"spinner": true});
        const email = document.getElementById("email" + this.domKey).value;
        const password = !this.props.withoutPassword ? document.getElementById("password" + this.domKey).value : null;
        let termsAndConditions = false;
        if (this.state.termsAndConditions !== false) {
            termsAndConditions = document.getElementById("termsAndConditions" + this.domKey).checked;
        }

        if (!(() => {
            let ok = true;
            let errors = {};
            if (!email) {
                errors.email = formatMessage(messages.emailRequiredError);
                ok = false;
            }
            if (!this.props.withoutPassword) {
                if (!password) {
                    errors.password = formatMessage(messages.passwordRequiredError);
                    ok = false;
                } else {
                    const repeatPassword = document.getElementById("repeatpassword" + this.domKey).value;
                    if (password !== repeatPassword) {
                        errors.password = formatMessage(messages.passwordsDoNotMatchError);
                        ok = false;
                    }
                }
            }
            if (this.state.termsAndConditions !== false) {
                if (!termsAndConditions) {
                    /*
                     * Auto submit, and T&C is the only thing missing:
                     */
                    if (ok && this.props.autoSubmit && event === null) {
                        this.setState({"spinner": true});
                        this.onTermsAndConditionsRequired(this.state.termsAndConditions, (accepted) => {
                            if (accepted) {
                                this.onSignup(email, password, true);
                                this.setState({"spinner": false});
                            } else {
                                this.setState({"spinner": false});
                            }
                        });
                    }
                    errors.termsAndConditions = formatMessage(messages.acceptTermsAndConditionsError);
                    ok = false;
                }
            }
            if (!ok) {
                this.setState({
                    'errors': errors
                });
            }
            return ok;
        })()) {
            this.setState({"spinner": false});
            return;
        }

        this.onSignup(email, password, termsAndConditions);
        this.setState({"spinner": false});
    }

    clickLogin(event) {
        event.preventDefault();
        this.onLogin();
    }

    setPasswordFieldType(type) {
        var fields = document.getElementsByClassName("password-field");
        for (var i = 0; fields.length; i++) {
            fields.item(i).type = type;
        }
    }
    showPasswords() {
        this.setPasswordFieldType('text');
    }
    hidePasswords() {
        this.setPasswordFieldType('password');
    }

    showTermsAndConditions(event) {
        event.preventDefault();
        this.setState({"showTermsAndConditions": true});
    }

    termsAndConditionsInnerHtml() {
        var element = document.getElementById("terms-and-conditions-text" + this.domKey);
        if (typeof(element) !== "undefined" && element) {
            element.innerHTML = this.state.termsAndConditions;
        }
    }

    static isEmptyObject(object) {
        if (typeof(Array.isArray) !== 'undefined' && Array.isArray(object) && object.length === 0) {
            return true;
        }
        for (var prop in object) {
            if (object.hasOwnProperty(prop)) {
                return false;
            }
        }
        return true;
    }

    prefill() {
        const emailField = document.getElementById("email" + this.domKey);
        if (!this.prefillDone && typeof(emailField) !== 'undefined' && emailField) {
            this.prefillDone = true;
            if (typeof(this.fields.email) !== "undefined") {
                emailField.value = this.fields.email;
            }
            const passwordField = document.getElementById("password" + this.domKey);
            if (typeof(this.fields.password) !== "undefined" && typeof(passwordField) !== 'undefined' && passwordField) {
                passwordField.value = this.fields.password;
                document.getElementById("repeatpassword" + this.domKey).value = this.fields.password;
            }
        }
    }

    componentDidMount() {
        this.prefill();
        this.termsAndConditionsInnerHtml();

        if (this.props.autoSubmit && (typeof(this.props.errors) === "undefined" || SignupForm.isEmptyObject(this.props.errors)) && !this.autoSubmitDone) {
            this.autoSubmitDone = true;
            this.clickSignup(null);
        }
    }

    componentDidUpdate() {
        this.prefill();
        this.termsAndConditionsInnerHtml();
    }

    getAppRoot() {
       var candidates = document.getElementsByClassName('reactive-auth-login');
       if (candidates.length > 0) {
           return candidates.item(0);
       } else {
           return document.body;
       }
    }

    renderPasswordFields() {
        const {formatMessage} = this.props.intl;

        return (
            <div>
                <input className="col-sm-5 password-field" type="password" id={"password" + this.domKey} placeholder={formatMessage(messages.signupPasswordFieldPlaceholder)} name="password" />
                <input className="col-sm-5 password-field" type="password" id={"repeatpassword" + this.domKey} placeholder={formatMessage(messages.signupRepeatPasswordFieldPlaceholder)} name="repeatpassword" />
                <div className="col-sm-2 eyeholder" onMouseDown={() => {this.showPasswords();}} onMouseUp={() => {this.hidePasswords();}} onMouseOut={() => {this.hidePasswords();}}></div>

                <p className="terms">
                    <FormattedMessage
                        id="signupPasswordRequirements"
                        defaultMessage="*Passwords must be minimum 6 characters long."
                    />
                </p>
            </div>
        );
    }

    render() {
        const {formatMessage} = this.props.intl;

        if (this.state.spinner) {
            return (
                <Loader
                    type="ThreeDots"
                    color="#00BFFF"
                    height="200"
                    width="200"
                />
            );
        }
        const formElementsStyle = {'display': this.props.visible ? 'block' : 'none'};
        return (
            <form method="post" onSubmit={(event) => { this.clickSignup(event); }}>
                <div style={formElementsStyle}>
                    {
                        (() => {
                            if (typeof(this.state.errors) !== "undefined" && typeof(this.state.errors.email) !== "undefined") {
                                return (
                                    <div className="error col-sm-12 alert alert-danger">{this.state.errors.email}</div>
                                );
                            } else {
                                return ( <span></span> );
                            }
                        })()
                    }

                    <input className="col-sm-12" type="text" id={"email" + this.domKey} placeholder={formatMessage(messages.signupEmailFieldPlaceholder)} name="email" />

                    {
                        (() => {
                            if (typeof(this.state.errors) !== "undefined" && typeof(this.state.errors.password) !== "undefined") {
                                return (
                                    <div className="error col-sm-12 alert alert-danger">{this.state.errors.password}</div>
                                );
                            } else {
                                return ( <span></span> );
                            }
                        })()
                    }

                    {
                        (() => {
                            if (!this.props.withoutPassword) {
                                return this.renderPasswordFields();
                            } else {
                                return ( <span></span> );
                            }
                        })()
                    }
                    {
                        (() => {
                            if (this.state.termsAndConditions !== false) {
                                return (
                                    <div className="terms">
                                        {
                                            (() => {
                                                if (typeof(this.state.errors) !== "undefined" && typeof(this.state.errors.termsAndConditions) !== "undefined") {
                                                    return (
                                                        <div
                                                            className="error col-sm-12 alert alert-danger">{this.state.errors.termsAndConditions}</div>
                                                    );
                                                } else {
                                                    return (<span></span>);
                                                }
                                            })()
                                        }

                                        <div className="terms-field">
                                            <label className="terms">
                                                <input type="checkbox" id={"termsAndConditions" + this.domKey} name="termsAndConditions"
                                                                                value="true"/>
                                                <FormattedMessage
                                                    id="signupTermsAndConditionsLabel"
                                                    defaultMessage={'I agree to the {termsAndConditions}'}
                                                    values={{
                                                        "termsAndConditions": <a onClick={(event) => { this.showTermsAndConditions(event); }}
                                                                                 id="terms-and-conditions-link"
                                                                                 href="/termsofservice"
                                                                                 target="_blank">
                                                            <FormattedMessage id="termsAndConditionsLink" defaultMessage="Terms and Conditions"/>
                                                        </a>
                                                    }}
                                                />
                                            </label>
                                        </div>

                                        <Modal show={this.state.showTermsAndConditions} onShow={() => { this.termsAndConditionsInnerHtml(); }} container={this.getAppRoot()}>
                                            <ModalTitle>
                                                <h2 className="terms-and-conditions-title">
                                                    <FormattedMessage id="termsAndConditionsPopupTitle" defaultMessage="Terms and Conditions"/>
                                                </h2>
                                            </ModalTitle>
                                            <ModalBody>
                                                <div id={"terms-and-conditions-text" + this.domKey} className="terms-and-conditions-text"></div>
                                            </ModalBody>
                                            <ModalFooter>
                                                <Button onClick={(event) => { this.setState({"showTermsAndConditions": false}); }} className="bt btn-success">
                                                    <FormattedMessage id="closeButton" defaultMessage="Close" />
                                                </Button>
                                            </ModalFooter>
                                        </Modal>
                                    </div>
                                );
                            } else {
                                return ( <span></span> );
                            }
                        })()
                    }

                    <div>
                        <input type="submit" value={formatMessage(messages.signupButton)}/>
                    </div>
                </div>

                <p className="center-text" style={formElementsStyle}>
                    <FormattedMessage
                        id="alreadyHaveAnAccount"
                        defaultMessage={'Already have an account? {logInHereLink}'}
                        values={{
                            "logInHereLink": <a onClick={(event) => {this.clickLogin(event);}} href="/login">
                                <FormattedMessage id="logInHereLink" defaultMessage="Log in here" />
                            </a>
                        }}
                    />
                </p>

            </form>
        );
    }
}

SignupForm.propTypes = {
    intl: intlShape.isRequired,
};

export default injectIntl(SignupForm);
