
import React, {Component} from 'react';
import {FormattedMessage, intlShape, injectIntl, defineMessages} from 'react-intl';
import LoginOperations from "./LoginOperations";
import TermsAndConditions from "./TermsAndConditions";
import axios from 'axios';
import {Button} from "react-bootstrap";
import querystring from 'querystring';
import NewPassword from "./NewPassword";

class ClaimAccounts extends Component {
    constructor(props) {
        super(props);

        const loginOpsProps = {
            intl: this.props.intl,
            authorizationCodeCallback: typeof(props.authorizationCodeCallback) === 'function' ? props.authorizationCodeCallback : undefined,
            authBaseUri: props.authBaseUri,
            clientId: props.clientId,
            codeAuthorizeUri: props.codeAuthorizeUri,
            onEmailConflict: (payload, intermediateToken) => {
                alert('Forgot password failed with failure to resolve email conflict');
            },
            onTermsAndConditions: (jwt, termsAndConditions) => {
                this.setState({
                    "jwt": jwt,
                    "termsAndConditions": termsAndConditions
                });
            }
        };
        this.loginOperations = new LoginOperations(loginOpsProps);
        this.domKey = "" + Math.random();
        this.state = {
            'errors': {},
            'language': typeof(props.language) !== 'undefined' ? props.language : 'en',
            'termsAndConditions': false,
            'jwt': false,
            'intermediateJwt': false,
            'accounts': [],
            'claimEmail': false,
            'claimPasswordAccount': false
        };
        this.passwordField = React.createRef();
        this.pullAccounts();
    }

    pullAccounts() {
        axios.get(this.props.authBaseUri+'/v1/login/idp/claimaccounts/'+this.props.claimId+'?code='+encodeURIComponent(this.props.code), {
        })
            .then((response) => {
                const data = response.data;
                if (typeof(data.accounts) !== 'undefined' && typeof(data.intermediateJwt) !== 'undefined' && typeof(data.claimEmail) !== 'undefined') {
                    this.setState({'accounts': data.accounts, 'intermediateJwt': data.intermediateJwt, 'claimEmail': data.claimEmail});
                } else {
                    alert('Invalid response data on claim accounts');
                }
            })
            .catch((error) => {
                alert('Claim accounts failure');
            })
    }

    declineTermsAndConditions() {
        document.location.href = this.props.logoutUri;
    }

    accountName(account) {
        if (account.displayName) {
            return account.displayName;
        } else if (account.firstName) {
            if (account.lastName) {
                return account.firstName + " " + account.lastName;
            } else {
                return account.firstName;
            }
        } else if (account.lastName) {
            return account.lastName;
        } else {
            return "No Name";
        }
    }

    claimAccount(account, password) {
        if (account.hasPassword && typeof(password) === 'undefined') {
            this.setState({'claimPasswordAccount': account});
        } else {
            var data = {'id': account.identity.id};
            if (typeof(password) !== 'undefined') {
                data.password = password;
            }
            axios.post(this.props.authBaseUri + '/v1/login/idp/claimaccount', querystring.stringify(data), {
                'headers': {
                    'Authorization': 'Bearer ' + this.state.intermediateJwt
                }
            })
                .then((response) => {
                    const data = response.data;
                    if (typeof (data.jwt) !== 'undefined') {
                        const jwt = data.jwt;
                        this.loginOperations.loggedIn(this.state.language, jwt);
                    } else {
                        alert('Claim account failed with bad response');
                    }
                })
                .catch((error) => {
                    alert('Claim account failed');
                });
        }
    }

    claimAccountWithPassword() {
        this.passwordField.current.getWrappedInstance().validatePassword((password) => {
           this.claimAccount(this.state.claimPasswordAccount, password);
        });
    }

    createNewAccount() {
        axios.post(this.props.authBaseUri+'/v1/login/idp/claimemail', querystring.stringify({'email': this.state.claimEmail, 'allowLogin': true}), {
            'headers': {
                'Authorization': 'Bearer '+this.state.intermediateJwt
            }
        })
            .then((response) => {
                const data = response.data;
                if (typeof(data.jwt) !== 'undefined') {
                    const jwt = data.jwt;
                    this.loginOperations.loggedIn(this.state.language, jwt);
                } else {
                    alert('Claim email failed with bad response');
                }
            })
            .catch((error) => {
                alert('Claim email failed');
            });
    }

    renderClaimPasswordAccount() {
        return (
            <div className="reactive-auth-login">
                <p>Please set a new password for your account:</p>
                <NewPassword ref={this.passwordField}/>
                <Button onClick={() => {
                    this.claimAccountWithPassword();
                }} className="btn btn-success">
                    <FormattedMessage id="submitForgotPasswordButton" defaultMessage="Save and continue"/>
                </Button>
            </div>
        );
    }

    render() {
        if (this.state.termsAndConditions === false) {
            if (this.state.claimPasswordAccount !== false) {
                return this.renderClaimPasswordAccount();
            }
            if (this.state.accounts.length > 0) {
                return (
                    <div className="reactive-auth-login">
                        <p>
                            <FormattedMessage
                                id={'idpConflictProceedClaimEmail'}
                                defaultMessage={'You have successfully confirmed your email address {email}. Please use an existing account or create a new account:'}
                                values={{'email': this.state.claimEmail}}
                            />
                        </p>
                        <ul>
                            {
                                (() => {
                                    let options = this.state.accounts.map((account) => {
                                        return (
                                            <li key={account.identity.id}>
                                                {this.accountName(account.identity)} {account.identity.email ? account.identity.email : "(No primary email)"}
                                                <Button onClick={() => {
                                                    this.claimAccount(account);
                                                }} className="btn btn-success">
                                                    <FormattedMessage id="claimExistingAccountButton"
                                                                      defaultMessage="Use this account"/>
                                                </Button>
                                            </li>
                                        );
                                    });
                                    if (this.state.claimEmail) {
                                        options[options.length] = (
                                            <li>
                                                <p><FormattedMessage
                                                    id="questionCreateNewAccountAndDisconnectOthers"
                                                    defaultMessage="Would you like to create a new account using {email}? This email address will be removed from the other accounts."
                                                    values={{'email': this.state.claimEmail}}/></p>
                                                <Button onClick={() => {
                                                    this.createNewAccount();
                                                }} className="btn btn-success">
                                                    <FormattedMessage id="createNewAccountLink"
                                                                      defaultMessage="Create new account"/>
                                                </Button>
                                            </li>
                                        );
                                    }
                                    return options;
                                })()
                            }
                        </ul>
                    </div>
                );
            } else if (this.state.claimEmail) {
                return (
                    <div className="reactive-auth-login">
                        <p>
                            <FormattedMessage
                                id={'idpConflictProceedClaimSignupWithEmail'}
                                defaultMessage={'You have successfully confirmed your email address {email}. You can now proceed with signing up:'}
                                values={{'email': this.state.claimEmail}}
                            />
                        </p>
                        <div>
                            <Button onClick={() => {
                                this.createNewAccount();
                            }} className="btn btn-success">
                                <FormattedMessage id="createNewAccountLink"
                                                  defaultMessage="Create new account"/>
                            </Button>
                        </div>
                    </div>
                );
            } else {
                return (
                    <div>
                        <p>
                            <FormattedMessage
                                id={'idpConflictClaimSomethingWentWrong'}
                                defaultMessage={'Something went wrong during the signup process. There are no apparent ways to continue this signup. Please try again or contact support.'}
                            />
                        </p>
                    </div>
                )
            }
        } else {
            return (
                <div className="reactive-auth-login">
                    <TermsAndConditions onAccept={() => { this.loginOperations.acceptTermsAndConditions(this.state.jwt); }} onDecline={() => { this.declineTermsAndConditions(); }} termsAndConditions={this.state.termsAndConditions} />
                </div>
            );
        }
    }
}

ClaimAccounts.propTypes = {
    intl: intlShape.isRequired
};

export default injectIntl(ClaimAccounts);
