
import React, {Component} from 'react';
import {Button}from 'react-bootstrap';
import {FormattedMessage, intlShape, injectIntl, defineMessages} from 'react-intl';
import querystring from 'querystring';
import axios from 'axios';
import LoginOperations from './LoginOperations';
import TermsAndConditions from "./TermsAndConditions";
import NewPassword from "./NewPassword";

const messages = defineMessages({
});

class ForgotPassword extends Component {
    constructor(props) {
        super(props);

        const loginOpsProps = {
            intl: this.props.intl,
            authorizationCodeCallback: typeof(props.authorizationCodeCallback) === 'function' ? props.authorizationCodeCallback : undefined,
            authBaseUri: props.authBaseUri,
            clientId: props.clientId,
            codeAuthorizeUri: props.codeAuthorizeUri,
            onEmailConflict: (payload, intermediateToken) => {
                alert('Forgot password failed with failure to resolve email conflict');
            },
            onTermsAndConditions: (jwt, termsAndConditions) => {
                this.setState({
                    "jwt": jwt,
                    "termsAndConditions": termsAndConditions
                });
            }
        };
        this.loginOperations = new LoginOperations(loginOpsProps);
        this.state = {
            'errors': {},
            'language': typeof(props.language) !== 'undefined' ? props.language : 'en',
            'termsAndConditions': false,
            'jwt': false
        };
        this.passwordField = React.createRef();
    }

    declineTermsAndConditions() {
        document.location.href = this.props.logoutUri;
    }

    submit() {
        const code = this.props.code;
        this.passwordField.current.getWrappedInstance().validatePassword((password) => {
            const requestBody = {
                'code': code,
                'password': password
            };
            axios.post(
                this.props.authBaseUri+'/v1/login/idp/forgotpassword/'+this.props.claimId, querystring.stringify(requestBody), {
                    'xsrfCookieName': null,
                    'withCredentials': true
                }
            )
                .then((response) => {
                    var loginResponse = response.data;
                    var jwt = loginResponse.jwt;
                    if (typeof(jwt) !== 'undefined' && jwt) {
                        this.loginOperations.loggedIn(this.state.language, jwt);
                    } else {
                        alert('Forgot password step did not produce a JWT');
                    }
                })
                .catch((error) => {
                    alert('Forgot password step in login process failed');
                });
        });
    }

    render() {
        if (this.state.termsAndConditions === false) {
            return (
                <div className="reactive-auth-login">
                    <h1>Reset password</h1>
                    <p>Please reset your password by typing your new password twice below:</p>

                    <NewPassword errors={this.state.errors}ref={this.passwordField}/>

                    <div>
                        <Button onClick={() => {
                            this.submit();
                        }} className="btn btn-success">
                            <FormattedMessage id="submitForgotPasswordButton" defaultMessage="Save and continue"/>
                        </Button>
                    </div>
                </div>
            );
        } else {
            return (
                <div className="reactive-auth-login">
                    <TermsAndConditions onAccept={() => { this.loginOperations.acceptTermsAndConditions(this.state.jwt); }} onDecline={() => { this.declineTermsAndConditions(); }} termsAndConditions={this.state.termsAndConditions} />
                </div>
            );
        }
    }
}

ForgotPassword.propTypes = {
    intl: intlShape.isRequired
};

export default injectIntl(ForgotPassword);
