import {addLocaleData} from 'react-intl';
import en from 'react-intl/locale-data/en';
import nb from 'react-intl/locale-data/nb';
import nn from 'react-intl/locale-data/nn';
import sv from 'react-intl/locale-data/sv';

import nbMessages from './lang/nb';
import svMessages from './lang/sv';

import enEdstepMessages from './lang/theme/edstep/en';
import nbEdstepMessages from './lang/theme/edstep/nb';
import svEdstepMessages from './lang/theme/edstep/sv';

import enLearnplaygroundMessages from './lang/theme/learnplayground/en';
import nbLearnplaygroundMessages from './lang/theme/learnplayground/nb';
import svLearnplaygroundMessages from './lang/theme/learnplayground/sv';

import enEdplanMessages from './lang/theme/edplan/en';
import nbEdplanMessages from './lang/theme/edplan/nb';
import svEdplanMessages from './lang/theme/edplan/sv';

addLocaleData([...en, ...nb, ...nn, ...sv]);

const messages = {
    "nb": nbMessages,
    "sv": svMessages
};
const themes = {
    "edstep": {
        "en": enEdstepMessages,
        "nb": nbEdstepMessages,
        "sv": svEdstepMessages
    },
    "learnplayground": {
        "en": enLearnplaygroundMessages,
        "nb": nbLearnplaygroundMessages,
        "sv": svLearnplaygroundMessages
    },
    "edplan": {
        "en": enEdplanMessages,
        "nb": nbEdplanMessages,
        "sv": svEdplanMessages
    }
};

const getMessages = function (lang, theme) {
    let m = {};
    if (messages.hasOwnProperty(lang)) {
        m = messages[lang];
    }
    /*
     * Let the theme override default messages
     */
    if (themes.hasOwnProperty(theme)) {
        let t = themes[theme];
        if (t.hasOwnProperty(lang)) {
            t = t[lang];
            for (let k in t) {
                if (t.hasOwnProperty(k)) {
                    m[k] = t[k];
                }
            }
        }
    }

    return m;
};

export default getMessages;
